<template>
    <div>
        <!-- Modal -->
<div class="modal fade AddModal" id="AddModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
  <div class="modal-dialog">
    <div class="modal-content">
      <div class="modal-header">
         <h5 class="modal-title" id="AddModalModalLabel">Add Time Slot</h5>
        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
          <span aria-hidden="true">&times;</span>
        </button>
      </div>
      <div class="modal-body">
           <div class="headline ml-2"><b>Working Time List</b></div>
<form @submit.prevent="AddTimeSolt">
   <div class="modal-body">
        <div class="headline ml-2"><b>Working Time List</b></div>
        <!--date-picker-area-->
         <div class="date-picker-area">
             <span class="start-date-picker"><i class="icon-start-time"></i><p>Start</p></span>  
         </div>
   </div>
</form>
      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
        <button type="button" class="btn btn-primary">Save changes</button>
      </div>
    </div>
  </div>
</div>
    </div>
</template>

<script>
import DatePicker from 'vue2-datepicker';
export default{
    name:'AddList',
  components: { DatePicker },
    data(){
        return {
            //開始與結束日期
            StartDate:'',
            EndDate:'',
            //WeekDays
            WeekDays:[],
            StartTime:'',
            EndTime:'',
            //驗證訊息
            msgStartDate:"",
            msgEndDate:"",
            msgDateError:"",
            msgWeekDays:"",
            msgStartTime:"",
            msgEndTime:"",
            msgTimeError:"",
            checkDisabled:false,

            AddSuccessMessage:'',
            AddFailMessage:'',

        }    
    },
    methods: {
         AddTimeSolt(){

         }
    },
    created(){
        this.AddTimeSolt();
    },    

}


</script>





